import React from "react";
import { useSelector, useDispatch } from "react-redux";
import CartItem from "../components/CartItem";
import { clearCart } from "../redux/cartSlice";
import { Link } from "react-router-dom";

const CartPage = () => {
  const items = useSelector((s) => s.cart.items); // Get cart items from Redux
  const dispatch = useDispatch(); // Get Redux dispatch function

  // Calculate total price of all items in cart
  const total = items.reduce((acc, i) => acc + i.price * i.quantity, 0);

  return (
    <div className="container section">
      {/* Page header with title and link back to shopping */}
      <div className="pageHeader">
        <h2>Shopping Cart</h2>
        <Link to="/" className="btn btn--light"> Continue Shopping</Link>
      </div>

      {/* If cart is empty, show a message */}
      {items.length === 0 ? (
        <p className="muted">Your cart is empty.</p>
      ) : (
        <>
          {/* List of cart items */}
          <div className="cartList">
            {items.map((i) => (
              <CartItem key={i.id} item={i} />
            ))}
          </div>

          {/* Cart summary section */}
          <div className="cartSummary">
            <div className="summaryRow">
              <span>Total</span>
              <strong>${total.toFixed(2)}</strong>
            </div>

            {/* Checkout button */}
            <button className="btn btn--primary">Checkout</button>

            {/* Clear cart button */}
            <button className="btn btn--ghost" onClick={() => dispatch(clearCart())}>
              Clear Cart
            </button>
          </div>
        </>
      )}
    </div>
  );
};

export default CartPage;
